import React from 'react';

const Sidebar = ({ users, userId }) => {
  return (
    <ul className="ck-user-group ck-sidebar">
      {users.map(user => {
        return (
          <li key={user.userId} className="ck-user-list">
            <div className="ck-user-circle" style={{ backgroundColor: user.color }} >{user.userName[0].toUpperCase()}</div>
            <div className="ck-username"><span className={userId===user.userId  ? "ck-me" : ""}>{user.userName} {userId===user.userId ? ` | Me` : ''}</span></div>
          </li>
        );
      })}
    </ul>
  );
};

export default Sidebar;
